import React, { useRef, useState } from 'react';
import classes from './GetStadiumInfo.module.css';

function GetStadiumInfo(props) {

    const userInput = useRef();
    const[typedInput, setTypedInput] = useState('');

    function findStadiumSubmitHandler(event) {
        event.preventDefault();
        const locationQuery = userInput;
        props.onSetLocation(locationQuery);
        setTypedInput('');
    }

    function stadiumTypedHandler(event){
        setTypedInput(event.target.value);
    }

    return (
        <>
            <div
                className={classes.test_field}>
                <form
                onSubmit={findStadiumSubmitHandler}
                className={classes.form_handler}>
                    <input 
                    className={classes.input_handler}
                    type='text'
                    name="location_input"
                    placeholder="type a location here!"
                    ref= {userInput}
                    value= {typedInput}
                    onChange= {stadiumTypedHandler} />
                    <button className={classes.submit_handler}>Submit</button>
                </form>
      
            </div>
        </>
    )
}

export default GetStadiumInfo;