import React, { useState } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { displayActions } from '../store/store-index';
import classes from './StadiumInfoReadOut.module.css';
import back_arrow from './back_arrow.png';


function StadiumInfoReadOut(props) {
    
    const dispatch = useDispatch();
    const displayHandler = useSelector(state => state.showResults);
    
    const [userSelectedStadium, setUserSelectedStadium] = useState('');


    function locationWasSelectedHandler(name, location) {
        props.onLocationSelected(location);
        setUserSelectedStadium(name);
        dispatch(displayActions.hide());
    }

    function backToFullListHandler() {
        //setLocationsAmountSelector(true);
        dispatch(displayActions.show());
        setUserSelectedStadium('');
    }

    return (
        <>
            {
                displayHandler ?
                    props.locations.map(element => {
                        return (
                            <div className={classes.readout}>
                                <h3 className={classes.items}
                                    onClick={() => locationWasSelectedHandler(element.name, element.location)}
                                >{element.name}</h3>
                            </div>);
                    }) :
                    props.locations.filter(element => element.name === userSelectedStadium).map(element => {
                        return (<div className={classes.big_readout}>
                            <img src={back_arrow}
                                className={classes.back_badge}
                                onClick={backToFullListHandler}
                                alt=''/>
                            <h3 className={classes.single_item}

                            >{element.name}</h3>
                        </div>);
                    })
            }
        </>
    )
}

export default StadiumInfoReadOut;