import React, { useState, useEffect } from 'react';
import { GoogleMap, LoadScript, Marker, InfoWindow } from '@react-google-maps/api';
import { useDispatch } from 'react-redux';
import GetStadiumInfo from './GetStadiumInfo';
import StadiumInfoReadOut from './StadiumInfoReadOut';
import {displayActions} from '../store/store-index';

import classes from './MapContainer.module.css';

const MapContainer = () => {

    const dispatch = useDispatch();

    const [userMadeNextQuery, setUserMadeNextQuery] = useState(false);
    const [stadiumsHolder, setStadiumsHolder] = useState([]);
    const [locations, setLocations] = useState([]);
    const [selection, setSelection] = useState({});
    const [showLocations, setShowLocations] = useState(true);
    const [mapZoom, setMapZoom] = useState(3.5);
    const mapStyles = {
        height: '100vh',
        width: "100%",
        margin: 'auto',
        marginTop: '100px'
    };
    const [center, setCenter] = useState({
        lat: 40,
        lng: -97.7431
    });

    //grab stadium info on render
    async function getStadiumInfo() {

        const stadiumInfo = await fetch("https://raw.githubusercontent.com/cageyjames/GeoJSON-Ballparks/master/ballparks.geojson");

        const stadiumInfoJson = await stadiumInfo.json();
        console.log(stadiumInfo);
        //bad request check
        if (stadiumInfo.status !== 200) {
            setShowLocations(false);
            setMapZoom(3.5);
            setCenter({
                lat: 40,
                lng: -97.7431
            });
            return;
        }

        const stadiums = [];
        for (let i = 0; i < stadiumInfoJson.features.length; i++) {
            stadiums[i] = {
                name: stadiumInfoJson.features[i].properties.Ballpark,
                location: {
                    lat: stadiumInfoJson.features[i].geometry.coordinates[1],
                    lng: stadiumInfoJson.features[i].geometry.coordinates[0]
                }
            }
        }

        setStadiumsHolder(stadiums);
    }

    //send api request based on user input
    async function userQueryHandler(userInput) {
        setLocations([]);
        setUserMadeNextQuery(true);
        setShowLocations(true);
        let url = "https://api.foursquare.com/v2/venues/search?near=" + userInput.current.value + "&radius=100000&limit=10&categoryId=4bf58dd8d48988d18c941735&client_id=XK5W5QAM0CYNAE2IHJ2NGJJ0WYBMTKJGIYZVWTCLTTPJF0B4&client_secret=XOY0TYQKJXO1EV4YBXZMRF3GQ5YC1GUZLFXB5XWVUANW3KIV&v=20210726";
        const stadiumInfo = await fetch(url);
        const jStadiumInfo = await stadiumInfo.json();

        if (jStadiumInfo.meta.code !== 200) {
            setMapZoom(3.5);
            setCenter({
                lat: 40,
                lng: -97.7431
            });
            setShowLocations(false);
            return;
        }

        const mapResults = [];
        let counter = 0;
        //if the promise was bad, print error message, return
        if (jStadiumInfo.response.venues.length === 0) {
            setShowLocations(false);
            return;
        }

        for (let i = 0; i < stadiumsHolder.length; i++) {
            console.log(jStadiumInfo.response.venues[0].location.lat);
            if (Math.abs(stadiumsHolder[i].location.lat - jStadiumInfo.response.venues[0].location.lat) < 5 &&
                Math.abs(stadiumsHolder[i].location.lng - jStadiumInfo.response.venues[0].location.lng) < 5) {
                mapResults[counter++] = {
                    name: stadiumsHolder[i].name,
                    location: {
                        lat: stadiumsHolder[i].location.lat,
                        lng: stadiumsHolder[i].location.lng
                    }
                };

            }
        }
        dispatch(displayActions.show());
        setLocations(mapResults);
        setCenter({
            lat: jStadiumInfo.response.venues[0].location.lat,
            lng: jStadiumInfo.response.venues[0].location.lng
        });
        setMapZoom(5.5);
    }

    function selectLocation(element) {
        setSelection(element);
    }

    function goToLocationHandler(location) {
        setMapZoom(12);
        setCenter({
            lat: location.lat,
            lng: location.lng
        });

    }

    useEffect(() => {
        getStadiumInfo();
    }, []);

    return (
        <div className={classes.user_view}>
            <section className={classes.info_readout}>
                <GetStadiumInfo onSetLocation={userQueryHandler} />
                {
                    showLocations ?
                        < StadiumInfoReadOut
                            onLocationSelected={goToLocationHandler}
                            onUserMadeNextQuery={userMadeNextQuery}
                            className={classes.items}
                            locations={locations}
                        /> : <h2>Error: There was nothing to show for that area :(</h2>
                }
            </section>
            <LoadScript
                googleMapsApiKey='AIzaSyDt7UkFiL_-59O-MTBBSh6mtIt3LUQ6WCc'>
                <GoogleMap
                    mapContainerStyle={mapStyles}
                    zoom={mapZoom}
                    center={center}>
                    {showLocations ? locations.map(element => {
                        return (
                            <Marker key={element.location.lat}
                                position={element.location}
                                name={element.name}
                                onClick={() => { selectLocation(element) }} />)
                    }) : null}
                    {
                        showLocations ? selection.location &&
                            (
                                <InfoWindow
                                    position={selection.location}
                                    clickable={true}
                                    onCloseClick={() => { setSelection({}) }} >
                                    <p>{selection.name}</p>
                                </InfoWindow>
                            ) : null
                    }
                </GoogleMap>
            </LoadScript>
        </div>

    )
}

export default MapContainer;
